body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,ul{
  padding: 0;
}

button {
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  border: none;
  font-size: 1rem;
  cursor: pointer;
  outline: none;
}

input,select{
  outline: none;
  border: none;
}

.App{
  color: rgb(230, 230, 230);
}

.list-container{
  width: 35%;
  min-height: 100vh;
  background-color: #4b5970;
}

.user-container{
  width: 65%;
  min-height: 100vh;
  background-color: #2d3542;
}

.section-title{
  font-weight: 100;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif
}


/* HELPERS */

* {
  box-sizing: content-box;
}

ul{
  list-style: none;
}

.flex{
  display: flex;
}

.flex-col{
  flex-direction: column;
}

.justify-center{
  justify-content: center;
}

.space-around{
  justify-content: space-around;
}

.space-between{
  justify-content: space-between;
}

.align-center{
  align-items: center;
}

/* ************************************** */
.user-list{
    
}

.control-panel{
    width: 100%;
}

.add-user-btn{
    background-color: black;
    color: rgb(212, 211, 211);
    transition: all 0.2s;
    width: 2rem;
    height: 2rem;
}

.add-user-btn:hover{
    background-color: rgb(53, 48, 48);
}

.user-preview{
    margin: 0.5rem;
    transition: all 0.4s;
    cursor: pointer;
    border-radius: 5px;
}

.details-container{
}

.user-preview:hover{
    background-color: #354661;
}

.preview-avatar{
    width: 4rem;
    height: 4rem;
    margin: 1rem;
}

.delete-btn{
    background-color: rgba(0,0,0,0);
    color: rgb(212, 211, 211);
    transition: all 0.2s;
}

.delete-btn:hover{
    color: black
}

.user-details , .user-list{
    padding: 1rem;
}

.detail-container{
    margin: 1rem;
}

.detail-avatar{
    width: 10rem;
    height: 10rem;
}

.detail{
    font-size: 1.5rem;
}

.description{
    color: rgb(192, 192, 192);
}


.modal-data{
    height: 95%;
    width: 95%;
    margin: 0 auto;
    border: solid black 1px;
    background-color: #4b5970;
    color: white;
}

.modal-data ::-webkit-input-placeholder{
    color: rgb(26, 25, 25);
}

.modal-data ::-ms-input-placeholder{
    color: rgb(26, 25, 25);
}

.modal-data ::placeholder{
    color: rgb(26, 25, 25);
}

.edit-form{
    height: 100%;
}

.edit-input{
    margin: 1rem;
    padding: 0.5rem;
    background-color: rgb(192, 192, 192);
    border-radius: 5px;
    
}


.add-btn{
    background-color: rgba(0,0,0,0);
    color: #2d3542;
    transition: all 0.2s;
}

.add-btn:hover{
    color: black
}


.ReactModal__Content {
    opacity: 0;
  }
  
  .ReactModal__Content--after-open {
    opacity: 1;
    transition: opacity 250ms;
  }
  
  .ReactModal__Content--before-close {
    opacity: 0;
  }
